@font-face {
    font-family: "Future";
    src: url("fonts/Kenney_Future_Narrow.ttf");
}

:root{
	--main-background: #0b0c0f;
	--main-fonts-color: #fff;
	--main-decor-color:#19B20E;
  	--main-header-background:#21252e;
	--main-font-family: 'Future', sans-serif;
  	--green: #A0DB44;
  	--blue:#00a9e2;
  	--title-background:#19B20E;
}

*{
	margin: 0;
	padding: 0;
	scroll-behavior: smooth;
	/* background: var(--main-background); */
}

main{
	padding: 0;
	width: 100%;
	height: 100%;
	background: var(--main-background);
}

/*────────────────── 
       header
──────────────────*/

header{
	margin: 0 auto;
	width: 100%;
	height: 70px;
	display: flex;
    align-items: center; 
	justify-content: space-around; 
    background: transparent;
	position: fixed;
	top: 0;
	transition: 0.3s;
	z-index: 5;
}
.nav-show{
	opacity: 0;
}

header:hover{
	opacity: 1;
	background: var(--main-header-background);
}

.logo img{
	padding-top: 5px;
	height: 50px;
	cursor: pointer;
}

.nav-bar{
	list-style:none;
	position: relative;
	display: inline-flex;
}

a.nav-link{
	margin: 2px;
	padding: 5px 10px;
	text-decoration: none;
	color: var(--main-fonts-color);
	font-family: var(--main-font-family);
	cursor: pointer;
	text-transform: uppercase;
}

.active{
	background: var(--main-decor-color);
}

.nav-link:hover {
	color: #000000;
    background: var(--main-decor-color);
}

/*──────────────────
       home
──────────────────*/

#home{ 	
	margin: auto;
	height: 100vh;
	color: var(--main-fonts-color);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
}

#home .background{
	/* background: url('pictures/newfeaturepic.png') no-repeat; */
    background: url('pictures/logo5.png') no-repeat;
	background-size: cover;
	background-position:center;
    position: absolute;
    top: 0px;
    bottom: 0;
    left: 0;
    right: 0;
	opacity:.20;
}  

.intro {
	text-align:center;
    color: var(--main-fonts-color);
	z-index: 1;
	margin: auto;
	padding: 20px;
}

.intro  p{
    margin: 5px;
	font-size:1.2rem;
	font-family: var(--main-font-family);
	text-align:center;
}

.intro  a{
    margin: 10px;
	font-size:2rem;
	font-family: var(--main-font-family);
	text-align:center;
	text-decoration: none;
	color: var(--main-fonts-color);
}

.intro  a:hover{
    color: var(--main-decor-color);
	text-shadow: 0 0 50px var(--main-decor-color);
}

h3{
	padding-bottom: 15px;
    letter-spacing: normal;
    font-family: var(--main-font-family);
	font-style: normal;
	font-size: 60px;
	color: var(--main-fonts-color);
	text-shadow: 0px 0px 15px var(--title-background);
}

/*──────────────────
  pyramid numbers
──────────────────*/

#pyramidnumbers{ 	
	margin: auto;
	height: 100vh;
	color: var(--main-fonts-color);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
}

#pyramidnumbers .background{
	/* background: url('pictures/newfeaturepic.png') no-repeat; */
    background: url('pictures/newfeaturepic.png') no-repeat;
	background-size: cover;
	background-position:center;
    position: absolute;
    top: 0px;
    bottom: 0;
    left: 0;
    right: 0;
	opacity:.20;
} 

#pyramidnumbers .small-link{
	font-size: 1.2rem;
	margin: 0px;
	text-decoration: underline;
}

/*──────────────────
   privacy policy
──────────────────*/

#privacypolicy{ 	
	margin: auto;
	/* height: 100vh; */
	color: var(--main-fonts-color);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
	background: var(--main-background);
}

#privacypolicy #boxing{
	width: 1920px;
	margin-top: 80px;
}

@media (max-width: 2016px){
	#privacypolicy #boxing{
		width: 95%;
	}
}

#privacypolicy ul{
	margin-left: 50px;
}

/*──────────────────
   Ts and Cs
──────────────────*/

#tsandcs{ 	
	margin: auto;
	/* height: 100vh; */
	color: var(--main-fonts-color);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
	background: var(--main-background);
}

#tsandcs #boxing{
	width: 1920px;
	margin-top: 80px;
}

@media (max-width: 2016px){
	#tsandcs #boxing{
		width: 95%;
	}
}

@media (min-height: 845px){
	#tsandcs #boxing{
		height: calc(100vh - 80px);
	}
}